    opacity: 1;
    
}
#mimg p .rb button {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 1.5rem;
    border: 2px solid rgb(255, 255, 255);
    /* transition: 0.7s ease-in-out; */
}
#mimg p .rb a{

    text-decoration: none;
    color: rgb(255, 255, 255);
}



