/* ----------------------------- custom card css ---------------------------- */

.author .row  .card_section {
    margin-top:40px ;
}
.author .row  .card_section .card{
    
    border: none;
    width: 20rem;
    height: 25rem;
    overflow: hidden;
    border-radius: 20px;
}
.author .row  .card_section .card .card-img{
    object-fit: cover;
    border-radius: 20px;
    height: 100%;
    transition: 1s;
    
}
.author .row  .card_section .card:hover .card-img
{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


.author .row  .card_section .card .card-body{
    border-radius: 20px;
    height: 4rem;
    position: absolute;
    bottom: 0;
    color: #fff;
    background-color: rgba(17, 17, 17, 0.507);
    text-align: center;
    transition: 1s;
    width: 100%;
    cursor: pointer;
}

.author .row  .card_section  .card .card-body .card-title
{
    font-size: 1.5rem;
}



